 funcionamiento simple, pero útil en el momento de detección de celos, los podómetros, en ganado bovino, son utilizados para medir la cantidad de pasos realizados por un animal, debido a que durante el estro o celo, las vacas aumentan su actividad y tienden a caminar más, este podómetro envía los datos a un computador o celular alertando el aumento de pasos frente a días anteriores, dando así un indicio de posible celo.")]),_c('p',[_vm._v("Estos dispositivos vienen tanto en collares como en pulseras que se colocan por encima de los corvejones y así guardan y registran la información. Estos dispositivos tienen sus ventajas y desventajas, dentro de las principales desventajas encontramos que no son muy útiles en animales que se encuentran bajo estabulación y /o amarrados.")])])])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img34.png"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h3',[_vm._v("Dispositivos de detección de monta")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-sm mb-5 mb-sm-0"}),_c('p',[_vm._v("Existen diferentes tipos de dispositivos para la detección de la monta que generalmente trabajan bajo el sistema de marcar el animal montado debido a la aceptación de la monta, ya sea con cápsulas que cambian de color, aplicación de pintura en la base de la cola o el sacro, y los sistemas electrónicos los cuales se inserta como un microchip en la base de la cola, los toros probadores o toros desviados tendrán un lector del microchip en un cinturón, al momento de realizar la monta este enviara un aviso del número del microchip al computador o celular del encargado de la producción para así identificar a la hembra montada. ")])])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img35.png"),"alt":"Imagen decorativa"}})])])])])],1)],1)],1)}
